.applyLeave{
    box-sizing: border-box;

    /* margin-top: 100px; */
    /* margin-left:1vw; */
    height:89vh;
    padding:1rem;
    background: #ffffff;
    /* box-shadow: 2px 2px 2px 2px; */
    border:1px solid black;
    border-radius: 5px;
    margin-right: 20px;
    padding-left: 20px;
    
}
.applyLeave form div{
  box-sizing: border-box;
  /* border:1px solid black; */
  padding:0.7rem;
  
}

form{
  display:grid;
  grid-template-rows:1fr 1fr 1fr 1fr 1fr;
}

label , input , select , span{
  display:inline-block;
  
}
label{
  font-weight: bold;
}
span{
  /* border:1px solid red; */
}
.reason , .uploadDoc , .TypeOfLeave{
  display: grid;
  grid-template-columns: 30fr 70fr;
}

.reason input,.TypeOfLeave select {
  box-sizing: border-box;
  padding:0.3rem;
  width:40vw;
  height:6vh;
  border-radius:3px;
  border:none;
  box-shadow: 2px 2px 4px black;
  font-size: 1.2rem;
}

span#warning{
  font-size: 0.9rem;
  color:red;
}
.apply button{
  box-sizing: border-box;
  padding:0.7rem;
  width:40vw;
  font-size: 1rem;
  border-radius:3px;
  border:none;
  box-shadow: 2px 2px 4px black;
  background: #05b7e8;
  margin-left:15vw;
  color:white;
  transition: box-shadow 500ms;
  cursor:pointer;
}

.apply button:hover{
  box-shadow: 4px 4px 8px black;
}

#sl{
  padding:0.7rem;
  font-size:0.8rem;
  font-weight: bold;
}
